@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.image-grid--v2 {
    grid-column: 1/-1;
    --rows: 3;
    --cols: 5;
    --border: 14px;
    background: conic-gradient(
            from 90deg at var(--border) var(--border),
            $color-gray2 90deg,
            $color-gray4 0
        )
        calc(-1 * var(--border)) calc(-1 * var(--border)) /
        calc(100% / var(--cols)) calc(100% / var(--rows));
}

.image-grid--v2 img {
    &.inView {
        animation-name: fadeIn;
        animation-duration: 0.5s;
        overflow: hidden;
        flex-basis: calc(100% / 5);
        object-fit: cover;
        max-width: calc(100% / 5);
    }
    &:not(.inView) {
        opacity: 0;
    }
}

.img_1,
.img_6,
.img_11 {
    animation-delay: 0s;
}

.img_2,
.img_7,
.img_12 {
    animation-delay: 0.05s;
}

.img_3,
.img_8,
.img_13 {
    animation-delay: 0.1s;
}

.img_4,
.img_9,
.img_14 {
    animation-delay: 0.15s;
}

.img_5,
.img_10,
.img_15 {
    animation-delay: 0.2s;
}
