.image-grid--v3 {
    grid-column: 1/-1;
    display: flex;
    justify-content: center;
    overflow: hidden;
    &:not(.inView) {
        opacity: 0;
    }
    --rows: 3;
    --cols: 5;
    --border: 14px;
    background: conic-gradient(
            from 90deg at var(--border) var(--border),
            $color-gray2 90deg,
            $color-gray4 0
        )
        calc(-1 * var(--border)) calc(-1 * var(--border)) /
        calc(100% / var(--cols)) calc(100% / var(--rows));
}

.image-grid--v3.inView .sides {
    animation: 0.7s curtain cubic-bezier(0.86, 0, 0.07, 1) 0.4s both;
    display: grid;
    grid-template-columns: 50vw 50vw;
}

.image-grid--v3 .side {
    display: flex;
    flex-wrap: wrap;
}
.image-grid--v3 .side img {
    flex-basis: calc(100% / 4);
    object-fit: cover;
    max-width: calc(100% / 4);
}

@keyframes curtain {
    0% {
        grid-gap: 100vw;
    }
    100% {
        grid-gap: 0;
    }
}
