@keyframes slideInFromLeft100 {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes scaleIn {
    0% {
        transform: scale(1.3);
    }
    100% {
        transform: scale(1);
    }
}

.row {
    display: flex;
    overflow: hidden;
}

.image-grid--v4 {
    grid-column: 1/-1;
    .box {
        position: absolute;
        width: 40%;
        transform: translate(10%, 50%);
        background: white;
        padding: 2rem 1rem;
        &.inView {
            animation: slideInFromLeft100 1s;
        }
        &:not(.inView) {
            opacity: 0;
        }
    }
}

.image-grid--v4 img {
    &.inView {
        animation: slideInFromLeft100 1s, scaleIn 4s;
        overflow: hidden;
        flex-basis: calc(100% / 5);
        object-fit: cover;
        max-width: calc(100% / 5);
    }
    &:not(.inView) {
        opacity: 0;
    }
}
