@keyframes slideInFromLeft100 {
    0% {
        opacity: 0;
        transform: scale(0, 0) rotate(5deg);
    }
    100% {
        opacity: 1;
        transform: scale(1, 1) rotate(0deg);
    }
}

.row {
    display: flex;
}

.image-grid--v1 {
    grid-column: 1/-1;
}

.image-grid--v1 img {
    &.inView {
        animation-name: slideInFromLeft100;
        animation-duration: 0.5s;
        overflow: hidden;
        flex-basis: calc(100% / 5);
        object-fit: cover;
        max-width: calc(100% / 5);
    }
    &:not(.inView) {
        opacity: 0;
    }
}

.img_1,
.img_6,
.img_11 {
    animation-delay: 0s;
}

.img_2,
.img_7,
.img_12 {
    animation-delay: 0.05s;
}

.img_3,
.img_8,
.img_13 {
    animation-delay: 0.1s;
}

.img_4,
.img_9,
.img_14 {
    animation-delay: 0.15s;
}

.img_5,
.img_10,
.img_15 {
    animation-delay: 0.2s;
}
